import { Produto } from './../../entity/Produto';
import { ProdutoServiceProvider } from './../../providers/produto-service/produto-service';
import { Utils } from './../../entity/Utils';
import { ToastController } from 'ionic-angular';
import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';

@IonicPage()
@Component({
  selector: 'page-produto-images',
  templateUrl: 'produto-images.html',
  providers: [ProdutoServiceProvider]
})
export class ProdutoImagesPage {
  showImage1: boolean;
  showImage2: boolean;
  showImage3: boolean;
  produtoUrl:string;
  model: Produto;

  constructor(public navCtrl: NavController,
    public toastCtrl: ToastController,
    public navParams: NavParams) 
    {
      this.produtoUrl = Utils.getUrlBackEnd() + "produto/";
      if (this.navParams.data.produto) {
        this.model = this.navParams.data.produto;
        if (this.model.imagem1 != null) 
          this.showImage1 = true;
        else
          this.showImage1 = false;

        if (this.model.imagem2 != null) 
          this.showImage2 = true;
        else
          this.showImage2 = false;

        if (this.model.imagem3 != null) 
          this.showImage3 = true;
        else
          this.showImage3 = false;

      } else {
        this.model = new Produto();
      }
    }
  
  
  presentToast(msg) {
    let toast = this.toastCtrl.create({
      message: msg,
      duration: 3000,
      position: 'bottom'
    });
  
    toast.onDidDismiss(() => {
      console.log('Dismissed toast');
    });
  
    toast.present();
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad ProdutoEditPage');
  }

}



